@import url(https://fonts.googleapis.com/css2?family=Noto+Sans:wght@700&display=swap);
:root {
  --theme-color: white;
  --primary-color: #337ab7;
  --secondary-color: #f7c331;
  --text-color-inverse: white;
  --text-color: #404040;
  --navbar-shallow-color: #e5e5e5;
  --menu-mobile-background-color: #3a4460;
}

body {
  font-family: "Montserrat", sans-serif;
  background-color: white;
  background-color: var(--theme-color);
  color: #404040;
  color: var(--text-color);
  padding-top: 0px;
}

a {
  text-decoration: none;
  color: #404040;
  color: var(--text-color);
}

.profile-img {
  width: 85%;
  max-width: 300px;
  border-radius: 50%;
}

.profile-img-section {
  position: relative;
}

@media (min-width: 950px) {
  .profile-img {
    width: 100%;
    max-width: 410px;
    transform: scaleX(-1);
    border-radius: 50%;
  }
}

.profile-info {
  margin-bottom: 25px;
}

.profile-details {
  font-size: 17px;
  font-weight: 500;
  line-height: 1.3;
  max-width: 450px;
  margin: 20px auto;
}

.profile-title {
  font-size: 35px;
  font-weight: bold;
  margin: 0;
  color: var(--primary-color);
  font-family: "Noto Sans", sans-serif;
}

@media (min-width: 950px) {
  .profile-info {
    margin-bottom: 0px;
  }

  .profile-title {
    font-size: 60px;
  }

  .profile-details {
    font-size: 17px;
    margin: 30px 0;
  }
}

.horizontal-padding {
  padding-left: 25px;
  padding-right: 25px;
}
.vertical-padding {
  padding-top: 25px;
}

@media (min-width: 950px) {
  .horizontal-padding {
    padding-left: 70px;
    padding-right: 70px;
  }
  .vertical-padding {
    padding-top: 70px;
  }
}

.profile {
  text-align: center;
}

@media (min-width: 950px) {
  .profile {
    text-align: left;
    display: flex;
    justify-content: space-between;
    max-width: 980px;
    margin: 0 auto;
    align-items: center;
  }
}

.experiences {
  display: flex;
  flex-direction: column;
}

.experiences-body {
  background-color: var(--primary-color);
  color: var(--text-color-inverse);
}

.wave {
  width: 100%;
  position: relative;
  top: 120px;
  z-index: -1;
}

.wave_bottom {
  transform: scaleY(-1);
  top: -120px;
}

.experiences-title {
  text-align: center;
  font-weight: bold;
  font-size: 30px;
  margin: 25px;
}

.experience-date {
  font-size: 25px;
  font-weight: bold;
  color: var(--secondary-color);
}

.experience-company {
  font-size: 20px;
  text-transform: uppercase;
  margin-top: 5px;
}

.experience-position {
  font-weight: 500;
  font-size: 18px;
  margin-top: 20px;
}

.experience-description {
  font-weight: 400;
  font-size: 15px;
  margin-top: 10px;
}

.experience {
  padding: 25px;
  max-width: 500px;
  margin: 0 auto;
}

@media (min-width: 950px) {
  .experiences-title {
    font-size: 50px;
    margin: 35px 0;
  }

  .experience-date {
    font-size: 40px;
  }

  .experience-company {
    font-size: 25px;
  }

  .experience-position {
    font-weight: bold;
    font-size: 20px;
    margin-top: 20px;
  }

  .experience-description {
    font-size: 20px;
    margin-top: 10px;
    line-height: 1.3;
  }

  .experience {
    padding: 0;
  }

  .experience_left {
    text-align: right;
  }

  .experiences-grid {
    display: grid;
    grid-template-columns: auto 2px auto;
    grid-gap: 20px 70px;
    gap: 20px 70px;
    padding: 70px;
    justify-content: center;
    max-width: 980px;
    margin: 0 auto;
  }

  .line {
    background-color: var(--text-color-inverse);
    position: relative;
  }

  .line::before,
  .line::after {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    background-color: var(--text-color-inverse);
    border-radius: 50%;

    position: absolute;
    top: -20px;
    left: -9px;
  }

  .line::after {
    top: 100%;
    z-index: 1;
  }
}

